import React, {useState} from 'react';
import ArrowLeftIcon from '@mui/icons-material/ArrowLeft';
import ArrowRightIcon from '@mui/icons-material/ArrowRight';
import {ApiSlides} from '../API/SliderApi';
import './Slider.css';
import { Link } from 'react-router-dom';

const Slider = () => {

    // USE STATE

    const [slides] = useState(ApiSlides)
    const [activeSlide, setActiveSlide] = useState(0)


    // STYLE

    const arrowStyle = 'rounded-full bg-grey flex justify-center items-center shadow-sm hover:cursor-pointer';

    const nextSlide = ()=>{
        if(activeSlide === slides.length -1)
        {
            setActiveSlide(0)
        }
        else{
            setActiveSlide(activeSlide + 1)
        }
    }

    const prevSlide = ()=>{
        if(activeSlide === 0){
            setActiveSlide(slides.length - 1)
        }
        else{
            setActiveSlide(activeSlide - 1)
        }
    }



    // RETURN FUNCTION

  return (
    <div className="parentDiv h-[540px] bg-white flex 
                    items-center justify-between mobile:hidden">
        

            {/* LEFT ARROW */}

        <div className={arrowStyle}>
            <ArrowLeftIcon style={{fontSize: '50px'}} onClick={prevSlide} />
        </div>


            {/* SLIDER */}

    {slides.map((slide, index)=>{
        if(index === activeSlide){
         return(
            <div className={`wrapper flex w-[100%] h-[500px] border-10px border-[#c0c0c0] 
                         justify-center items-center shadow-2xl rounded-lg overflow-hidden relative
                        ` + slide.background}>
            <div className="slide flex items-center justify-center h-[100%]">
            <div className="forImage flex flex-1 justify-center items-center h-[100%]">
                    <img
                        src={slide.src}
                        className='h-[100%] object-cover'
                        />
                </div>
                <div className="des flex flex-col flex-1 place-items-start justify-center ml-11 ">
                    <h2 className='text-[55px]'>{slide.content.h2}</h2>
                    <p className='text-[30px]'>{slide.content.p}</p>
                    <Link to="/products">
                    <button className="btn">Shop Now</button>
                    </Link>
                </div>
             </div>
        </div>
                    )
                }
            })}


            {/* RIGHT ARROW */}
        
        <div className={arrowStyle}>
            <ArrowRightIcon style={{fontSize: '50px'}} onClick={nextSlide} />
        </div>
    </div>
  )
}

export default Slider;